<template>
<div class="col mb-3">
  <div class="card h-100">
    <img src="https://via.placeholder.com/450x300" class="card-img-top" :alt="item.name">
    <div class="card-body text-center mb-3 h-100">
      <h5 class="card-title text-center">{{item.name}}</h5>
      <p class="card-text"><em>{{item.desc || "aucune description disponible"}}</em></p>
    </div>
    <div v-if="interact && item.prices.length > 0" class="card-body text-center">
      <div class="btn-group">
        <button type="button" class="btn btn-outline-dark" @click="addToCart({item, price: item.prices[0]})">
          <i class="bi bi-cart-plus-fill px-2"></i>
          <span>Ajouter</span>
        </button>
        <button type="button" class="btn btn-outline-dark dropdown-toggle dropdown-toggle-split" data-bs-toggle="dropdown" aria-expanded="false">
          <span class="visually-hidden">voir les tarifs</span>
        </button>
        <ul class="dropdown-menu">
          <li v-for="(p, i) in item.prices" :key="i">
            <a class="dropdown-item"
              href="#"
              @click="addToCart({item, price: p})">{{p.price}}&euro; pour {{p.quantity}}</a>
          </li>
        </ul>
      </div>
    </div>
  </div>
</div>
</template>
<script>
import {mapMutations} from 'vuex'

export default {
  name: "catalog-item",
  props: {
    item: Object,
    interact: {
      type: Boolean,
      default: true,
    },
  },
  methods: {
    ...mapMutations('cart', ['addToCart']),
  },
}
</script>
