<template>
  <section class="my-5" v-if="count > 0">
    <h3>Votre panier</h3>
    <table class="table table-striped">
      <thead>
        <tr>
          <th></th>
          <th>produit</th>
          <th class="text-center">prix unitaire</th>
          <th class="text-center">quantité</th>
          <th class="text-end">total</th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(p, i) in products" :key="i">
          <td width="10%">
            <img src="https://via.placeholder.com/450x300" class="card-img-top">
          </td>
          <td class="align-middle">{{p.product.name}}</td>
          <td class="align-middle text-center">{{p.price.price.toFixed(2)}}&euro;/{{p.price.quantity}}</td>
          <td width="10%" class="align-middle text-center">
            <input type="number"
              min="1"
              :value="p.quantity"
              class="form-control"
              @input="updateQuantity({item: p.product, price: p.price, quantity: parseInt($event.target.value)})"/>
          </td>
          <td class="align-middle text-end">{{(p.price.price * p.quantity).toFixed(2)}}&euro;</td>
          <td class="align-middle text-center">
            <button type="button"
              class="btn btn-outline-dark"
              title="supprimer du panier"
              @click="delFromCart({item: p.product, price: p.price})">
              <i class="bi bi-cart-dash-fill"></i>
            </button>
          </td>
        </tr>
      </tbody>
      <tfoot>
        <tr>
          <td colspan="3">total</td>
          <td width="10%" class="align-middle text-center">{{quantity}}</td>
          <td class="align-middle text-end">{{total.toFixed(2)}}&euro;</td>
          <td></td>
        </tr>
      </tfoot>
    </table>
  </section>
  <div v-else class="my-5">
    <p class="text-center lead">il n'y a pas d'article dans votre panier</p>
  </div>
</template>
<script>
import {mapGetters, mapMutations} from 'vuex'

export default {
  name: "cart-content",
  computed: {
    ...mapGetters('cart', ['products', 'total', 'quantity', 'count']),
  },
  methods: {
    ...mapMutations('cart', ['updateQuantity', 'delFromCart']),
  },
}
</script>
<style scoped>
img {
  width: 150px;
  height: auto;
}
</style>
