<template>
  <div id="map"></div>
</template>
<script>
import {mapGetters} from 'vuex'

export default {
  name: "map",
  mounted() {
    this.setupMap()
  },
  computed: {
    ...mapGetters('farm', ['position']),
  },
  methods: {
    setupMap() {
      let map = new window.google.maps.Map(this.$el, {
        center: this.position,
        zoom: 12,
      })
      new window.google.maps.Marker({
        position: this.position,
        map: map,
      })
    },
  },
}
</script>
<style scoped>
#map{
  height: 480px;
}
</style>
