      <input :value="term"
        @input="$emit('update:term', $event.target.value)"
        type="search"
        class="form-control"
        placeholder="bintje, poireaux,..."/>
    </div>
  </form>
</template>
<script>
export default {
  name: "filter",
  props: {
    categories: Array,
    term: String,
    category: String,
