import _ from 'lodash'

function getIndex(list, item, price) {
  return list.findIndex(p => _.isEqual(p.product, item) && _.isEqual(p.price, price))
}

const cartkey = "cart.list"

function dumpCart(list) {
  if (!list) {
    return
  }
  localStorage.setItem(cartkey, JSON.stringify(list))
}

function loadCart() {
  let str = localStorage.getItem(cartkey)
  if (!str) {
    return []
  }
  try {
    return JSON.parse(str) || []
  } catch(e) {
    console.log(e)
    return []
  }
}

const state = {
  products: loadCart(),
  distance: 0,
  prices: [
    {distance: 20, amount: 60},
    {distance: 30, amount: 80},
    {distance: 40, amount: 90},
    {distance: 60, amount: 100},
  ],
}

const getters = {
  distance: (state) => state.distance,
  products: (state) => state.products,
  prices: (state) => state.prices,
  count: (state) => state.products.length,
  total: (state) => state.products.reduce((p, c) => p+(c.price.price*c.quantity), 0),
  quantity: (state) => state.products.reduce((p, c) => p+c.quantity, 0),
  deliverable: (state, getters) => getters.total >= state.prices[0].amount,
  acceptable: (state, getters) => {
    if (state.distance <= state.prices[0].distance) {
      return true
    }
    let all = state.prices.map(p => p.distance <= state.distance && p.amount <= getters.total)
    return all.some(t => t)
  }
}

const mutations = {
  addToCart(state, {item, price}) {
    let ix = getIndex(state.products, item, price)
    if (ix >= 0) {
      state.products[ix].quantity++
      return
    }
    state.products.push({
      product: item,
      price: price,
      quantity: 1,
    })
    dumpCart(state.products)
  },
  delFromCart(state, {item, price}) {
    let ix = getIndex(state.products, item, price)
    if (ix < 0) {
      return
    }
    state.products.splice(ix, 1)
    // dumpCart(state.products)
  },
  updateQuantity(state, {item, price, quantity}) {
    let ix = getIndex(state.products, item, price)
    if (ix < 0) {
      return
    }
    state.products[ix].quantity = quantity
    dumpCart(state.products)
  },
  setDistance(state, distance) {
    distance /= 1000
    if (distance > 0 && distance < 1) {
      distance = 1
    }
    state.distance = distance
  },
}

let serv = new window.google.maps.DistanceMatrixService()

const actions = {
  distanceTo: ({commit}, {city, country, position}) => {
    let location = `${city}, ${country}`
    let latlng = new window.google.maps.LatLng(position)
    let req = {
      origins: [latlng],
      destinations: [location],
      travelMode: window.google.maps.TravelMode.DRIVING,
    }
    serv.getDistanceMatrix(req).then(resp => {
      if (!resp.rows) {
        return
      }
      let dist = 0
      resp.rows[0].elements.forEach(e => {
        if (dist == 0 || e.distance.value < dist) {
          dist = e.distance.value
        }
      })
      commit('setDistance', dist)
    })
  },
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
}
