<template>
  <div class="carousel slide" data-bs-ride="carousel" id="highlight" data-bs-interval="10000">
    <div class="carousel-inner">
      <div v-for="(gs, i) in groups" :key="i" :class="['carousel-item', {'active': i==0}]" >
        <div class="row">
          <Item v-for="(g, i) in gs" :key="i" :item="g" :interact="false"/>
        </div>
      </div>
    </div>
    <button class="carousel-control-prev" type="button" data-bs-target="#highlight" data-bs-slide="prev">
      <span class="carousel-control-prev-icon" aria-hidden="true"></span>
    </button>
    <button class="carousel-control-next" type="button" data-bs-target="#highlight" data-bs-slide="next">
      <span class="carousel-control-next-icon" aria-hidden="true"></span>
    </button>
  </div>
</template>
<script>

import Item from './Item.vue'
import {mapGetters} from 'vuex'
import _ from 'lodash'

export default {
  name: "carousel",
  computed: {
    ...mapGetters('catalog', ['highlight']),
    groups() {
      let list = [[]]
      this.highlight.forEach(p => {
        let last = _.last(list)
        if (last.length < 3) {
          last.push(p)
        } else {
          list.push([p])
        }
      })
      let last = _.last(list)
      if (last.length != 3) {
        list.pop()
      }
      return list
    }
  },
  components: {
    Item,
  },
}
</script>
