<template>
  <div class="">
    <div class="h-50 my-5">
      <div class="container align-middle py-5">
        <p class="w-75 mx-auto lead text-center">{{about}}</p>
      </div>
    </div>
    <div class="h-50 bg-light">
      <Map/>
    </div>
    <div class="h-50 my-5" v-if="showCarousel">
      <div class="container py-5">
        <Carousel/>
        <p class="text-center">
          <router-link :to="{name:'shop'}">
            <span>visitez notre shop et découvrez tous nos produits</span>
          </router-link>
        </p>
      </div>
    </div>
  </div>
</template>
<script>
import {about} from './constant.js'
import Carousel from './Carousel.vue'
import Map from './Map.vue'
import {mapGetters} from 'vuex'
export default {
  name: "home",
  data() {
    return {
      about,
    }
  },
  computed: {
    ...mapGetters('catalog', ['highlight']),
    showCarousel() {
      return this.highlight && this.highlight.length%3==0
    },
  },
  components: {
    Carousel,
    Map,
  }
}
</script>
