import _ from 'lodash'
import catalog from './sample.js'

const asc = "asc"
const desc = "desc"
const popular = "popular"

function sortByPrice(list) {
  return _.sortBy(list, i => _.chain(i.prices).sortBy('price').first().value().price)
}

function sortProducts(by, list) {
  if (by == asc) {
    list = sortByPrice(list)
  } else if (by == desc) {
    list = sortByPrice(list)
    list = _.reverse(list)
  } else if (by == popular) {
    //
  } else {
    list = _.sortBy(list, 'name')
  }
  return list
}

const state = {
  products: catalog,
  term: "",
  category: "",
  sort: "",
}

const getters = {
  highlight: (state) => state.products.filter(p => p.highlight),
  products: (state) => {
    let list = state.products.filter(p => {
      let word = state.term == "" || p.name.indexOf(state.term) >= 0
      let cat = state.category == "" || p.categories.indexOf(state.category) >= 0
      return word && cat
    })
    return sortProducts(state.sort, list)
  },
  categories: (state) => {
    let cs = state.products.map(i => i.categories).flat()
    return _.chain(cs).uniq().sort().value()
  },
  term: (state) => state.term,
  category: (state) => state.category,
  sort: (state) => state.sort,
  all: (state) => state.products.length,
  available: (_, getters) => getters.products.length,
}

const mutations = {
  setTerm(state, term) {
    state.term = term
  },
  setCategory(state, category) {
    state.category = category
  },
  setSort(state, sort) {
    state.sort = sort
  }
}

const actions = {

}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
}
