<template>
<main class="container">
  <section class="mt-5 mb-3 d-flex justify-content-between">
    <div class="d-flex">
      <Filter :categories="categories" v-model:term="term" v-model:category="category"/>
      <div v-if="available < all" class="mx-3 mt-2">
        <span>produits <strong>{{available}}/{{all}}</strong></span>
      </div>
    </div>
    <Sort v-model:by="sort"/>
  </section>
  <section class="my-5">
    <Pagination v-model:pages="pages" v-model:current="current" v-model:count="count"/>
    <div class="row row-cols-1 row-cols-md-4 g-4">
      <Item v-for="(i, x) in list" :item="i" :key="x"/>
    </div>
    <Pagination v-model:pages="pages" v-model:current="current" v-model:count="count"/>
  </section>
</main>
</template>
<script>

import Item from './Item.vue'
import Sort from './Sort.vue'
import Filter from './Filter.vue'
import Pagination from './Paginate.vue'
import _ from 'lodash'
import {mapGetters, mapMutations} from 'vuex'

export default {
  name: "shop",
  data() {
    return {
      count: 12,
      current: 1,
    }
  },
  computed: {
    ...mapGetters('catalog', ['products', 'categories', 'available', 'all']),
    term: {
      get() {
        return this.$store.getters.term
      },
      set(value) {
        this.setTerm(value)
      },
    },
    category: {
      get() {
        return this.$store.getters.category
      },
      set(value) {
        this.setCategory(value)
      },
    },
    sort: {
      get() {
        return this.$store.getters.sort
      },
      set(value) {
        this.setSort(value)
      },
    },
    list() {
      let ps = this.products
      if (this.count < this.available) {
        let starts = this.count * (this.current-1)
        let ends = starts + this.count
        ps = _.slice(ps, starts, ends)
      }
      return ps
    },
    pages() {
      if (this.available < this.count) {
        return 1
      }
      return Math.round(this.available/this.count)+1
    },
  },
  methods: {
    ...mapMutations('catalog', ['setTerm', 'setCategory', 'setSort']),
  },
  components: {
    Item,
    Sort,
    Filter,
    Pagination,
  },
}
</script>
