<template>
<nav class="my-3 d-flex justify-content-center">
  <form class="row row-cols-lg-auto g-3 align-items-center me-3">
    <label for="page" class="col m-0">Produits par page</label>
    <div class="col m-0">
      <select :value="count" class="form-select" id="page" @change="$emit('update:count', parseInt($event.target.value))">
        <option value="12">12</option>
        <option value="24">24</option>
        <option value="48">48</option>
      </select>
    </div>
  </form>
  <ul v-if="pages > 1" class="pagination justify-content-center">
    <li :class="['page-item', {disabled: current==1}]">
      <a class="page-link"
        href="#"
        @click="$emit('update:current', prev)">&laquo;</a>
    </li>
    <li v-for="i in pages"
      :key="i"
      :class="['page-item', {active: i == current}]"
      @click="$emit('update:current', i)">
      <a class="page-link" href="#">{{i}}</a>
    </li>
    <li :class="['page-item', {disabled: current==pages}]">
      <a class="page-link"
        href="#"
        @click="$emit('update:current', next)">&raquo;</a>
    </li>
  </ul>
</nav>
</template>
<script>

export default {
  name: "pagination",
  props: {
    pages: Number,
    current: Number,
    count: {
      type: Number,
      default: 12,
    },
  },
  computed: {
    prev() {
      return this.current > 1 ? this.current-1 : 1;
    },
    next() {
      return this.current < this.pages ? this.current+1 : this.pages
    },
  },
  emits: ["update:count", "update:current"],
}
</script>
